@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;400;500;600;700&display=swap);
* {
  margin: 0;
  font-family: "Quicksand";
  color: #333;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

.navbar {
  padding: 20px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
  color: green;
}

.navbar .links {
  margin-left: auto;
}

.navbar a {
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
}

.navbar a:hover {
  color: green;
}

#addTask {
  position: fixed;
  bottom: 30px;
  right: 30px; 
  width: 50px;
  height: 50px;
  border-radius: 50%;
  color:black;
  text-align: center;
  background: rgb(43, 255, 114);
  outline: white;
  border: none;
}

#addList {
  position: fixed;
  bottom: 10px;
  left: 10px; 
  display:block;
  width:50px;
  height:50px;
  border-radius: 50%;
  color:#f5f5f5;
  text-align:center;
  background: #555777;
  box-shadow: 0 0 3px gray;
}
*{
    margin: 0;
    padding: 0;
}
.board{
    display: flex;
    min-width: 100%;
    overflow-x: scroll;
    height: 40.5em;
}

.board::-webkit-scrollbar {
    display: none;
}
.task{
    border-radius: 5px;
    padding: 10px;
    color: #000;
    transition: opacity .2s ease-in-out;
    opacity: 1;
    box-shadow: 0 4px 2px -2px gray;
    margin-bottom: 10px;
    background-color: white;
}

.task:hover {
    opacity: .5;
}

.task-hidden{
    border-radius: 5px;
    margin-left: 10px;
    padding: 10px;
    color: #000;
    opacity: 1;
    box-shadow: 0 4px 2px -2px gray;
    margin-bottom: 10px;
}
* {
    margin:0;
    padding:0;
}

.task-list {
    padding: 1em;
    margin: 10px;
    color: black;
    border-radius: 5px;
    background-color: #ebecf0;
}

.note {
    background-color: transparent;
}

#title {
    padding: 15px;
    position: relative;
    background-color: transparent;
}

ul {
    list-style-type: none;
    padding: 0;
    margin: 0;
}

li {
    margin-bottom: 10px;
}

.drop-area {
    min-height: 50em;
    background-color: #ebecf0;
    padding: 10px;
    border-radius: 5px;
    min-width: 20em;
}
.overlay {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, .7);
    z-index: 1000;
}

.modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: #FFF;
    padding: 50px;
    z-index: 1000;
    width: 40em;
    height: 45em;
    border-radius: 5px;
}

label {
    text-align: left;
    display: block;
    font-size: 20px;

}

input, textarea, select {
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}
